import { Component, OnInit } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { Meta, Title } from '@angular/platform-browser';
import {
  AppNameService, CurrencyService, DataService, MainBarTitleService,
  TooltipComponent,
  TooltipService, WindowService, scrollToTop
} from '@front-shared-lib';
import { AdminPageContentComponent } from '../../misc/admin-page-content/admin-page-content.component';
import { AdminTopBarComponent } from '../../misc/admin-top-bar/admin-top-bar.component';

@Component({
  selector: 'app-settings',
  standalone: true,
  imports: [
    TooltipComponent,
    FormsModule,
    AdminPageContentComponent,
    AdminTopBarComponent
  ],
  templateUrl: './settings.component.html',
  styleUrl: './settings.component.scss'
})
export class SettingsComponent
implements OnInit {
  globallyEnabled = this.tooltipService.globallyEnabled;
  popperShowOnStart = this.tooltipService.popperShowOnStart;
  currencies = ['USD', 'UGX'];

  constructor(
    public dataService: DataService,
    private windowService: WindowService,
    private apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    public currencyService: CurrencyService,
    public tooltipService: TooltipService,
    private metaTagService: Meta,
    private titleService: Title
  ) { }

  ngOnInit(): void {
    this.mainBarTitle.title = 'Settings';
    this.setTitle();
    scrollToTop(this.windowService);
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: 'Easily view and manage your profile settings for personalized account management.'
    });
  }

  setTitle() {

  }
}

